
.loading-ct {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #1C78C0;
    opacity: .7;
    z-index: 999;
}

.loading,
.loading:before,
.loading:after {
    content: '';
    position: absolute;
    width: 10px;
    height: 40px;
    background-color: #fff;
    -webkit-animation: load 1s ease-in-out infinite;
            animation: load 1s ease-in-out infinite;
}

.loading {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(50%, 50%);
        -ms-transform: translate(50%, 50%);
            transform: translate(50%, 50%);
    -webkit-animation-delay: .16s;
            animation-delay: .16s;
}

.loading:before {
    left: -15px;
}

.loading:after {
    right: -15px;
    -webkit-animation-delay: .32s;
            animation-delay: .32s;
}

@-webkit-keyframes load {
    0%,
    80%,
    100% {
        height: 40px;
        -webkit-box-shadow: 0 0 #fff;
                box-shadow: 0 0 #fff;
    }
    10% {
        height: 50px;
        -webkit-box-shadow: 0 -20px #fff;
                box-shadow: 0 -20px #fff;
    }
}

@keyframes load {
    0%,
    80%,
    100% {
        height: 40px;
        -webkit-box-shadow: 0 0 #fff;
                box-shadow: 0 0 #fff;
    }
    10% {
        height: 50px;
        -webkit-box-shadow: 0 -20px #fff;
                box-shadow: 0 -20px #fff;
    }
}


